import React from 'react'
import { Form, Label, Input } from 'semantic-ui-react';
import './TextField.scss'

function TextField({
  input,
  label,
  type,
  meta: { asyncValidating, touched, error, warning },
  ...rest
}) {

  let labelColor;

  if (error) {
    labelColor = 'red'
  } else if (warning) {
    labelColor = 'yellow'
  }
  const message = error || warning;
  return (
    <Form.Field className='formContent'>

      <Input
        className='formInput'

        label={label}
        type={type}
        loading={asyncValidating}
        {...input}
        {...rest}
      />
      {touched && message &&
        <Label
          className='formLabel'
          basic
          color={labelColor}
          pointing='top'
        >
          {message}
        </Label>
      }
    </Form.Field>
  )
}

export default TextField;